import React from "react";
import {Link} from "react-router-dom";
import Background from "../images/camara.png";
import ReactAudioPlayer from 'react-audio-player';

import Artyom from 'artyom.js';
const Jarvis = new Artyom();

class Instructions extends React.Component {

    constructor (props, context){
        super(props, context);
        this.AudioRef = React.createRef();
        // Prepare simple state
        this.state = {
            audio : null,
            label:"",
        };

        this.onAudioLoaded = this.onAudioLoaded.bind(this);
      }
    
    componentDidMount(){
        if(!this.props.location.data){
            this.props.history.push("/sisben")
        }
        else{
            this.setState({
                audio : this.props.location.data.audio,
                label: this.props.location.data.label,
            })
            console.log(this.props.location.data)
            if(this.props.location.data.audio!=""){
                this.AudioRef.audioEl.current.src = require("../audios/"+ this.props.location.data.audio + ".mp3"); 
                console.log("Si hay audio")
            }
            else{
                Jarvis.say("Usted ha seleccionado la opción "+this.props.location.data.label +". Para continuar por favor presione sobre el botón azul y siga las instrucciones",{lang:"es-ES"})
                console.log(this.props.location.data.label)
                console.log("No hay audio")
            }
        }
        
        
    } 


    onAudioLoaded(){
        this.AudioRef.audioEl.current.play();
    }


    componentWillUnmount(){
        Jarvis.shutUp();
    }

    render(){
        //const {instrucciones, label} = this.props.location.data;
        return (
            <div className="container-fluid vh-100 " style={{display:"flex", flexDirection: "column", justifyContent:"center", alignItems:"center"}}>
                <h1>{this.state.label}</h1>
                <p>Por favor presione sobre el botón azul para activar la cámara y siga las instrucciones</p>
                <Link to={{
                    pathname:'/foto',
                    data:this.props.location.data
                    }} style={{textDecoration:'none'}}>
                        <div  style={{width:70, height: 70, borderRadius: "50%", backgroundImage: `url(${Background})`, backgroundSize: "cover"}}>
    
                        </div>
                </Link>


                <ReactAudioPlayer
                  src=""
                  //autoPlay
                  //controls
                  id = "audio"
                  ref={(element) => { this.AudioRef = element; }}
                  onCanPlay={this.onAudioLoaded}
                />
                
            </div>    
        );
    }
    
};

export default Instructions;