import React from "react";
import {Link} from "react-router-dom";
import ReactAudioPlayer from 'react-audio-player';

import Artyom from 'artyom.js';
const Jarvis = new Artyom();

class Start extends React.Component {

    constructor (props, context){
        super(props, context);

        // Prepare simple state
        this.state = {
            isAutorized: false
        };

     
    }
  
    componentDidMount(){
        //Jarvis.say("Buenos días. ¿cómo está usted?, bienvenido al trámite del Sisben",{lang:"es-ES"})
        let _this = this
        document.getElementById("buttonStart").addEventListener("click", function(){
            //Jarvis.say("Buenos días. ¿cómo está usted?, bienvenido al trámite del Sisben",{lang:"es-ES"});
            _this.setState({isAutorized:true});
        }, false);
    } 

    componentWillUnmount(){
        //Jarvis.shutUp();
    }

    render(){
        let content = ""

        if(this.state.isAutorized){
            content=
                <div className="container-fluid vh-100">
                    <h3>¿Tienes dudas en los tramites?</h3>
                    <h5>Soy Pipatón Virtual,  estoy para ayudarte</h5>
                    <div style={{display:"flex", flexDirection:"column", alignItems:"center", margin:"2em"}}>
                        <img src={require("../images/pipaton.jpg")} height="250em" style={{margin:"1em"}}  className="d-inline-block align-top" alt=""/>
                        <Link to={{
                            pathname:'/formulario',
                            }} style={{textDecoration:'none'}}><button type="button" className="btn btn-primary">Continuar</button></Link> 
                    </div>

                    <ReactAudioPlayer
                        src={require("../audios/1Saludo.mp3")}
                        autoPlay
                        
                        id = "audio"
                    />
                             
                </div>      
            
        }else{
            content=
                <div className="container-fluid vh-100">
                    <h1>Bienvenido  a la Sucursal virtual</h1>
                    <div style={{display:"flex", flexDirection:"column", alignItems:"center", margin:"2em"}}>
                        <img src={require("../images/logo.jpg")} height="250em" style={{margin:"1em"}}  className="d-inline-block align-top" alt=""/>
                        <button id="buttonStart" type="button"  className="btn btn-primary">Comenzar</button>
                    </div>
                             
                </div>      
            
        }
        
        return (content);
        
    }
};

export default Start;