import React from 'react';
import ReactAudioPlayer from 'react-audio-player';

class TramitesMenu extends React.Component {

  constructor (props, context){
    super(props, context);
    this.AudioRef = React.createRef();
    // Prepare simple state
    this.state = {
        audio : require("../audios/5OpcionTramites.mp3"),
        selected: false,
        urlPath: "/"
    };

    this.onAudioLoaded= this.onAudioLoaded.bind(this);
    this.optionSelected = this.optionSelected.bind(this);
    this.optionSelectedFinalized = this.optionSelectedFinalized.bind(this);
  }

  onAudioLoaded(){
    this.AudioRef.audioEl.current.play();
  }

  optionSelected(audioName, path){
    this.setState({
      audio:require("../audios/"+ audioName + ".mp3"),
      selected: true,
      urlPath: path
    });
  }

  optionSelectedFinalized(){
    if(this.state.selected){
      console.log("audio finalizado");
      this.props.history.push(this.state.urlPath);
    }
  }

  render(){
    return (
      <div className="container vh-100">
  
          <h1>{JSON.parse(localStorage.getItem('datos')).Nombres}</h1>
          <p>Nos alegra darle la bienvenida</p>
          <div className="container" style={{display:"flex", justifyContent:"center", flexFlow:"wrap"}}>
  
              <button type="button" className="btn btn-danger" style={{width: '10em', margin:"1em"}} onClick={()=>this.optionSelected("OpcionSisben", 'sisben')}>SISBEN</button>
              <button type="button" className="btn btn-info" style={{width: '10em', margin:"1em"}}>Carta catastral</button>
              <button type="button" className="btn btn-warning" style={{width: '10em', margin:"1em"}}>Estratificación</button>
  
              <ReactAudioPlayer
                  src={this.state.audio}
                  //autoPlay
                  //controls
                  id = "audio"
                  ref={(element) => { this.AudioRef = element; }}
                  onCanPlay={this.onAudioLoaded}
                  onEnded={this.optionSelectedFinalized}
              />
          </div>
      </div>
      
    )
  }
  
}
export default TramitesMenu