import React from "react";
import ReactAudioPlayer from 'react-audio-player';
import { Link } from 'react-router-dom';
import {InstruccionesIncluir, InstruccionesRetiro, InstruccionesEncuesta} from '../Datos/Data';

class ViewSisben extends React.Component {

    render(){
        
        return (
            <div className="container" style={{display:"flex", justifyContent:"center"}}>
                <div style={{ width:"80%", display: "flex", flexFlow:"wrap", justifyContent:"center" }}>
                
                <Link to={{
                    pathname:'/instrucciones',
                    data:InstruccionesIncluir
                    }} style={{textDecoration:'none'}}>
                    
                    <button type="button" className="btn btn-danger" style={{width: '10em', height:"6em", margin:"1em"}}>Inclusión y/o Modificación de Persona</button>    
                </Link>

                <Link to={{
                    pathname:'/instrucciones',
                    data:InstruccionesRetiro
                    }} style={{textDecoration:'none'}}>
                    
                    <button type="button" className="btn btn-info" style={{width: '10em', height:"6em", margin:"1em"}}>Retiro de Persona</button>
                </Link>

                <Link to={{
                    pathname:'/instrucciones',
                    data:InstruccionesEncuesta
                    }} style={{textDecoration:'none'}}>
                    
                    <button type="button" className="btn btn-warning" style={{width: '10em',height:"6em", margin:"1em"}}>Encuesta Nueva</button>
                </Link>
                    
                    
                    
                    <button type="button" className="btn btn-success" style={{width: '10em',height:"6em", margin:"1em"}}>Consulte Sisben</button>
                </div>

                <ReactAudioPlayer
                  src={require("../audios/OpciontramitesSisben.mp3")}
                  autoPlay
                  //controls
                  id = "audio"
              />
            </div>      
        );
    }
};

export default ViewSisben;