import React from "react";
import Webcam from "react-webcam";
import Gallery from 'react-grid-gallery';
import Background from "../images/camara.ico";
import {isMobile} from 'react-device-detect';
import {storage} from '../Services/FirebaseConfig';
import PopPop from 'react-poppop';

import Artyom from 'artyom.js';
const Jarvis = new Artyom();


 
const WebcamComponent = () => <Webcam />;

let IMAGES =[]
let FILES = []
const INSTRUCCIONES = [
  {titulo: "Cédula frontal", texto: "Tome una fotografía del lado frontal de su cédula"},
  {titulo: "Cédula trasera", texto: "Tome una fotografía del lado trasero de su cédula"},
  {titulo: "Recibo de energía", texto: "Tome una fotografía de su último recibo de energía eléctrica"},
]
let contador = 0

class WebcamCapture extends React.Component {

  constructor (props, context){
    super(props, context);

    // Prepare simple state
    this.state = {
      imgSrc: <div>nada</div>,
      images:[],
      imageUpload:null,
      titulo:"",
      texto:"",
      completed: false,
      uploading: false 
    };

    this.refreshState = this.refreshState.bind(this);
    this.onFileChanged = this.onFileChanged.bind(this);
    this.deleteImage = this.deleteImage.bind(this);

    // Load some commands to Artyom using the commands manager  
  }

  componentDidMount(){
    //Verifica si hay los datos necesarios que se envian por props
    if(!this.props.location.data){
      this.props.history.push("/sisben")
    }
    else{
      this.setState({
        titulo:this.props.location.data.instrucciones[contador].titulo,
        texto: this.props.location.data.instrucciones[contador].texto,
      })
  
      //Jarvis.say("Buenos días. ¿cómo está usted?, bienvenido al trámite del Sisben",{lang:"es-ES"})
      Jarvis.say(this.props.location.data.instrucciones[contador].texto,{lang:"es-ES"});
  
      const listRef = storage.ref(`/Imagenes/`).child(`${localStorage.getItem('cedula')}/`);
      listRef.listAll().then(function(res) {
        res.prefixes.forEach(function(folderRef) {
          // All the prefixes under listRef.
          // You may call listAll() recursively on them.
        });
        res.items.forEach(function(itemRef) {
          // All the items under listRef.
          console.log(itemRef)
        });
      }).catch(function(error) {
        // Uh-oh, an error occurred!
      });
    }
    
    
  }

  componentWillUnmount(){
    Jarvis.shutUp();
  }

  setRef = webcam => {
    this.webcam = webcam;
  };

  

  refreshState(value){
    
    if(value>0){
      if(contador<this.props.location.data.instrucciones.length-1){
        this.setState({completed:false});
        contador = contador+value;
        this.setState({
          titulo:this.props.location.data.instrucciones[contador].titulo,
          texto: this.props.location.data.instrucciones[contador].texto,
        });
  
        Jarvis.shutUp();
        Jarvis.say(this.props.location.data.instrucciones[contador].texto,{lang:"es-ES"});
        
      } else {
        this.setState({completed:true});
  
        Jarvis.shutUp();
        Jarvis.say("Presiona sobre el botón azul para finalizar",{lang:"es-ES"});
      }
    }
    else{
      if(contador>0 && !this.state.completed){
        this.setState({completed:false});
        contador = contador+value;
        this.setState({
          titulo:this.props.location.data.instrucciones[contador].titulo,
          texto: this.props.location.data.instrucciones[contador].texto,
        });

        Jarvis.shutUp();
        Jarvis.say(this.props.location.data.instrucciones[contador].texto,{lang:"es-ES"});
      }else if(contador>0 && this.state.completed){
        this.setState({completed:false});
        this.setState({
          titulo:this.props.location.data.instrucciones[contador].titulo,
          texto: this.props.location.data.instrucciones[contador].texto,
        });
        Jarvis.shutUp();
        Jarvis.say(this.props.location.data.instrucciones[contador].texto,{lang:"es-ES"});
      }
    }

    
    
  }

  uploadImage(file){
    const uploadTask = storage.ref(`/Imagenes/${localStorage.getItem('cedula')}/${this.props.location.data.label}/${this.state.titulo}`).put(file);
      this.setState({uploading:true})
    uploadTask.on('state_changed', 
    (snapshot)=>{
      //Progreso
      console.log(snapshot)
    }, (error)=>{
      //Error
      console.log(error);
      this.setState({uploading:false})
      alert("ha ocurrido un error, inténtelo de nuevo por favor")
    }, ()=>{
      //completado
      storage.ref(`/Imagenes/${localStorage.getItem('cedula')}/${this.props.location.data.label}/`).child(`${this.state.titulo}`).getDownloadURL()
       .then(fireBaseUrl => {
         console.log(fireBaseUrl)
         this.setState({images: IMAGES.push({src:fireBaseUrl, thumbnail: fireBaseUrl,  thumbnailWidth:300 , caption:this.state.titulo, tags: [{value: this.state.titulo, title: "cedula"}],})})
         this.refreshState(1);
         this.setState({uploading:false})
       })
    })

    
  }

  deleteImage(){
    this.setState({uploading:true})
    let name = "";
    if(this.state.completed){
      name = this.props.location.data.instrucciones[contador].titulo
    }else {
      if(contador>0){
        name = this.props.location.data.instrucciones[contador-1].titulo
      }
      else{
        name = this.props.location.data.instrucciones[contador].titulo
      }
      
    }
    console.log(name);
    const DeleteRef = storage.ref(`/Imagenes/${localStorage.getItem('cedula')}/${this.props.location.data.label}/`).child(`${name}`);
    DeleteRef.delete().then(()=> {
      // File deleted successfully
      console.log("imágen borrada correctamente")
      IMAGES.pop();
      this.setState({uploading:false});
      this.refreshState(-1)
    }).catch((error)=> {
      // Uh-oh, an error occurred!
      //this.setState({uploading:false})
      //alert("Lo sentimos, no se ha podido eliminar la imágen")
      console.log(error)
    });
    this.setState({uploading:true})
  }

  onFileChanged = e =>{
    if(e.target.files[0]){
      const image = e.target.files[0];
      //this.setState(()=>({imageUpload: image}));
      //console.log(this.state.imageUpload);
      this.uploadImage(image);
    }
  }

  capture = () => {
    if(!this.state.completed){
      console.log("Tomaste una foto")
      const imageSrc = this.webcam.getScreenshot();
      //console.log(imageSrc)

      fetch(imageSrc)
        .then(res => res.blob())
        .then(res => {
          console.log(res);

          this.uploadImage(res);
        })

      //this.setState({images: IMAGES.push({src:imageSrc, thumbnail: imageSrc, thumbnailWidth:300, caption:this.state.titulo, tags: [{value: this.state.titulo, title: "cedula"}],})})
      //IMAGES.push({src:imageSrc, thumbnail: imageSrc, thumbnailWidth:256, thumbnailHeight:144, caption:"Foto", tags: [{value: "Documento", title: "cedula"}],})
      //console.log(IMAGES)
      //this.refreshState();
    }else{
      console.log("terminado. empezando a subir archivos");

      this.props.history.push('/final');
    }
    
  };

  render(){
    const videoConstraints = {
      width: 1280,
      height: 720,
      facingMode: isMobile?{ exact: "environment" }:"user"//{ exact: "environment" }//"user"
    };

    return (
      <div className="container " style={{textAlign:"center"}}>
        <h1>{this.state.titulo}</h1>
        <p>{this.state.texto}</p>
        <div className="container-fluid mb-5">
          <Webcam
            audio={false}
            //height={"100%"}
            ref={this.setRef}
            screenshotFormat="image/jpeg"
            width={"100%"}
            videoConstraints={videoConstraints}
            mirrored = {false}
          />
          <small id="ayudacedula" className="form-text text-muted mb-4">Presiona sobre el botón  rojo para tomar la fotografía del documento</small>
          <button type="button" 
            className="btn btn-primary" 
            onClick={this.capture} 
            style={this.state.completed? {}:{width:70, height: 70, borderRadius: "50%", backgroundImage: `url(${Background})`, backgroundSize: "cover"}}>
              {this.state.completed? "Finalizar":""}
          </button>
          {!this.state.completed && 
            <input 
              type="file" 
              class="form-control-file" id="uploadImage" 
              onChange={this.onFileChanged}
              accept=".jpeg, .png, .jpg"/>}
        </div>

        <div className="container-fluid text-center" style={{display:"flex", flexDirection:"column", justifyContent:"center", textAlign:"center"}}>
          {/*this.state.imageSrc*/}
          <Gallery images={[...IMAGES]} backdropClosesModal={true} imageCountSeparator={" de "}/>
          {IMAGES.length>0 && <button type="button" class="btn btn-danger" style={{width:"15rem"}} onClick={this.deleteImage}>Eliminar última foto</button>}
        </div>

        

        <PopPop
            position="centerCenter"
            open={this.state.uploading}
            closeBtn={false}
            closeOnEsc={false}
            //onClose={() => this.toggleShow()}
            closeOnOverlay={false}
            contentStyle={{
              
              overflow:'hidden',
              maxWidth: '60vw',
              maxHeight: '40vh',
              width: '90vw',
              height: '90vh',
              padding:0,
              borderRadius: '1em',
              display: "flex",
              justifyContent: "center",
              alignItems: "center",

            
            }}>
              <h2 >Procesando...</h2>
        </PopPop>

      </div>
    );
  }
  
    
};

export default WebcamCapture;
  
  // https://www.npmjs.com/package/react-webcam
  