import React, { useState } from "react";
import {Link} from "react-router-dom";
import { useHistory } from "react-router-dom";
import Artyom from 'artyom.js';
import ReactAudioPlayer from 'react-audio-player';

import SpeechRecognition, { useSpeechRecognition } from 'react-speech-recognition';

import firebase from '../Services/FirebaseConfig';

const Jarvis = new Artyom();
let inputField = "";

class Form extends React.Component{

    constructor (props, context){
        super(props, context);
        this.AudioRef = React.createRef();
  
        // Add `this` context to the handler functions
        this.StartAssistant = this.StartAssistant.bind(this);
        this.StopAssistant = this.StopAssistant.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleChangeInput = this.handleChangeInput.bind(this);
        this.handleFocusInput = this.handleFocusInput.bind(this);
        this.onAudioLoaded = this.onAudioLoaded.bind(this);

        // Prepare simple state
        this.state = {
            User: "",
            isDictating:false,
            buttonClassActive: "btn btn-warning",
            buttonClassDisable: "btn btn-danger",
            isAnyActive: false,
            inputField:"",

            audio : require("../audios/2DatosPersonales.mp3"),

            dia: "",
            mes: "",
            ano: "",
            cedula :""
        };

     
    }


    onchangeValueInput = e =>{
        this.setState({
            [e.target.name]:e.target.value
        })
    }
      
    

    componentDidMount(){
        //Jarvis.say("Bríndenos sus datos por favor. Antes de empezar con su trámite, necesitamos que nos diga los siguientes datos",{lang:"es-ES"}) 
        //this.AudioRef.audioEl.current.play();
        this.onAudioLoaded();
        
    } 

    componentWillUnmount(){
        Jarvis.shutUp();
    }

    User = "";
    handleSubmit(evt) {
        evt.preventDefault();
        this.getUserData();
        //this.props.history.push('/tramites');
    }

    getUserData = () => {
        let fechaExpedicion = 
            document.getElementById("dia").value.padStart(2, "0") + 
            "/" +document.getElementById("mes").value.padStart(2, "0") +
            "/" + document.getElementById("ano").value
        console.log(fechaExpedicion);
        let ref = firebase.ref("Personas/" + document.getElementById("cedula").value);
        ref.on('value', snapshot => {
            const state = snapshot.val();
            if(state != null){
                if(state.FechaExpedicion == fechaExpedicion){
                    localStorage.setItem("cedula", document.getElementById("cedula").value);
                    localStorage.setItem("datos", JSON.stringify(state));
                    this.props.history.push('/tramites');
                }
                else{
                    this.AudioRef.audioEl.current.pause()
                    Jarvis.say("La fecha de expedición no coincide con el número de documento ingresado",{lang:"es-ES"})
                    alert("La fecha de expedición no coincide con el número de documento ingresado");
                }
                
            }
            else{
                this.AudioRef.audioEl.current.pause()
                Jarvis.say("Lo sentimos, El número de cédula ingresado no existe en nuestra base de datos",{lang:"es-ES"})
                alert("Lo sentimos, El número de cédula ingresado no existe en nuestra base de datos");
            }
            

        });
    }

    handleChangeInput(e) {
        const inputValue = e.target.value;
        this.User = inputValue;
    }

    onAudioLoaded(){
        this.AudioRef.audioEl.current.play();
    }

    handleFocusInput(audioName){
        /*Jarvis.shutUp();
        Jarvis.say(text,{lang:"es-ES"});*/
        this.AudioRef.audioEl.current.src = require("../audios/"+ audioName + ".mp3")
        this.setState({
            audio:require("../audios/"+ audioName + ".mp3")
        });
    }


    UserDictation = Jarvis.newDictation({
        continuous:true, // Enable continuous if HTTPS connection
        lang: "es-ES",
        onResult:function(text){
            // Do something with the text
            console.log(text);
            
            if(text!=""){
                document.getElementById(inputField).value = text;
            }
            
        },
        onStart:function(){
            console.log("Puedes empezar a dictar");
        },
        onEnd:function(){
            console.log("Se ha desactivado el reconocimiento de voz");
        }
    });

    StartAssistant( valueinput ){
        let _this = this;

        if(inputField == valueinput && this.state.isDictating){
            //this.UserDictation.stop();
            this.props.stop();
            this.setState({
                isDictating:false,
                //buttonClass:"btn btn-danger",
                inputField:""
            });
            inputField = ""
        } else if(!this.state.isDictating){
            Jarvis.fatality()
            //this.UserDictation.start();
            this.props.start();
            this.setState({
                isDictating:true,
                //buttonClass: "btn btn-warning",
                inputField: valueinput
            });
            inputField = valueinput
        } else if(inputField != valueinput && this.state.isDictating){
            this.props.stop();
            this.props.resetTranscript();
            this.props.start();
            inputField = valueinput
            this.setState({
                inputField: valueinput
            })
        }
    }

    StopAssistant(){

        this.UserDictation.stop();
        let _this = this;
        Jarvis.fatality().then(() => {    
        }).catch((err) => {
            console.error("Oops, ha ocurrido un error!", err);
        });    
    }

    updateText(transcript){
        if(this.state.inputField !=""){
            document.getElementById(this.state.inputField).value = transcript.replace(/ +/g, "");
            /*if(this.state.inputField =="cedula"){
                document.getElementById(this.state.inputField).value = transcript.replace(/ +/g, "");
            }else{
                document.getElementById(this.state.inputField).value = transcript
            }*/
            
        }
    }

    render(){
        const { transcript, resetTranscript } = this.props
        this.updateText(transcript);
        const {input} = this.state;
        let micro = ""
        if(!this.state.isDictating){
            micro = <img src={require("../images/micro2.webp")} height="20" alt="USA flag"/>
        }else{
            micro = <img src={require("../images/microred.png")} height="20" alt="USA flag"/>
        }
        return (
            <div className="container " style={{textAlign:"left"}}>
                <div style={{textAlign:"center"}}>
                    <h2>Brindenos sus datos por favor</h2>
                    <small id="ayudacedula" className="form-text text-muted">Antes de empezar con su trámite, necesitamos que nos diga los siguientes datos</small>
                    <br></br>
                </div>
                
                <form action="/instrucciones" onSubmit={this.handleSubmit}>
    
                    <div className="form-group">
                        <label htmlFor="cedula" className="font-weight-bold">Número de cédula:</label>
                        <div className="input-group text-align:center">   
                            <input type="text" className="form-control" id="cedula" placeholder="Escriba el número de su cédula" name="cedula" onFocus={()=>this.handleFocusInput("3NumeroCedula")} required onChange={this.onchangeValueInput}/> 
                            <span className="input-group-btn">
                                <button className={this.state.inputField=="cedula"? this.state.buttonClassActive : this.state.buttonClassDisable} type="button" onClick={()=>this.StartAssistant("cedula")} disabled={false}>
                                    <img src={this.state.inputField=="cedula"? require("../images/microred.png"):require("../images/micro2.webp") } height="20" alt="USA flag"/>
                                </button>
                            </span>
                        </div>
                        
                        <small id="ayudacedula" className="form-text text-muted">Por favor ingrese el número de documento SIN signos de puntuación.</small>
                    </div>
    
                    {/*<div className="form-group">
                        <label htmlFor="celular" className="font-weight-bold">Fecha de expedición:</label>
                        <div className="input-group text-align:center">   
                            <input type="date" className="form-control" id="fechaExpedicion" placeholder="Escriba la fecha de expedición de su documento" name="fechaExpedicion" onFocus={()=>this.handleFocusInput("4FechaExpedicion")} required/>
                            <span className="input-group-btn">
                                <button className={this.state.inputField=="fechaExpedicion"? this.state.buttonClassActive : this.state.buttonClassDisable} type="button" onClick={()=>this.StartAssistant("fechaExpedicion")} disabled={true}>
                                    <img src={this.state.inputField=="fechaExpedicion"? require("../images/microred.png"):require("../images/micro2.webp") } height="20" alt="USA flag"/>
                                </button>
                            </span>
                        </div> 
                        
                        <small id="ayudacedula" className="form-text text-muted">Por favor ingrese la fecha de expedición de su documento.</small>
                    </div>*/}

                    <div className="form-group">
                        <label htmlFor="dia" className="font-weight-bold">Día de expedición de su documento:</label>
                        <div className="input-group text-align:center">   
                            <input type="number" className="form-control" id="dia" placeholder="Escriba el día de expedición de su documento de identidad" name="dia" onFocus={()=>this.handleFocusInput("Dia")} min="1" max="31" required onChange={this.onchangeValueInput}/>
                            <span className="input-group-btn">
                                <button className={this.state.inputField=="dia"? this.state.buttonClassActive : this.state.buttonClassDisable} type="button" onClick={()=>this.StartAssistant("dia")} disabled={false}>
                                    <img src={this.state.inputField=="dia"? require("../images/microred.png"):require("../images/micro2.webp") } height="20" alt="USA flag"/>
                                </button>
                            </span>
                        </div> 
                        
                        {/*<small id="ayudacedula" className="form-text text-muted">Por favor ingrese el número que corresponde al día del mes de expedición de su documento.</small>*/}
                    </div>

                    <div className="form-group">
                        <label htmlFor="mes" className="font-weight-bold">Mes de expedición de su documento:</label>
                        <div className="input-group text-align:center">   
                            <input type="number" className="form-control" id="mes" placeholder="Escriba el mes de expedición de su documento de identidad" name="mes" onFocus={()=>this.handleFocusInput("Mes") } min="1" max="12" required onChange={this.onchangeValueInput}/>
                            <span className="input-group-btn">
                                <button className={this.state.inputField=="mes"? this.state.buttonClassActive : this.state.buttonClassDisable} type="button" onClick={()=>this.StartAssistant("mes")} disabled={false}>
                                    <img src={this.state.inputField=="mes"? require("../images/microred.png"):require("../images/micro2.webp") } height="20" alt="USA flag"/>
                                </button>
                            </span>
                        </div> 
                        
                        {/*<small id="ayudacedula" className="form-text text-muted">Por favor ingrese el número que corresponde al mes de expedición de su documento.</small>*/}
                    </div>

                    <div className="form-group">
                        <label htmlFor="ano" className="font-weight-bold">Año de expedición de su documento:</label>
                        <div className="input-group text-align:center">   
                            <input type="number" className="form-control" id="ano" placeholder="Escriba el año de expedición de su documento de identidad" name="ano" onFocus={()=>this.handleFocusInput("Año")} min="1800" required onChange={this.onchangeValueInput}/>
                            <span className="input-group-btn">
                                <button className={this.state.inputField=="ano"? this.state.buttonClassActive : this.state.buttonClassDisable} type="button" onClick={()=>this.StartAssistant("ano")} disabled={false}>
                                    <img src={this.state.inputField=="ano"? require("../images/microred.png"):require("../images/micro2.webp") } height="20" alt="USA flag"/>
                                </button>
                            </span>
                        </div> 
                        
                        {/*<small id="ayudacedula" className="form-text text-muted">Por favor ingrese el número que corresponde al año de expedición de su documento.</small>*/}
                    </div>
    
                    <button type="submit" className="btn btn-primary">  Guardar y Continuar  </button>
                </form>

                <ReactAudioPlayer
                    src={this.state.audio}
                    //autoPlay
                    //controls
                    id = "audio"
                    ref={(element) => { this.AudioRef = element; }}
                    onCanPlay={this.onAudioLoaded}
                />

            </div>
        );
    }

    
};

export default Form;